<template>
  <div id="clue">
    <main id="content">
      <img :src="require('../assets/img'+img_url)" :id="'clue_img'+id">
      <button v-if="hint != 'no_hint'" @click="open_hint(hint)" class="clue-hint-button hint-button">ヒント</button>
    </main>
  </div>
</template>

<script>

export default {
  name: 'Clue',
  props: {
    id: Number,
    img_url: String,
    hint: {
      type: String,
      default: 'no_hint'
    }
  },
  methods:{
    open_hint: function(content){
      this.$emit('open_hint',content)
    }
  }
}
</script>

<style>
img{
  width: 100%;
}
.clue-hint-button{
  margin: 6% 30% !important;
}
#clue{
  position: relative;
}
#content{
  padding-bottom: 10%;
}
</style>
