      </transition>
      <img src="./assets/img/logo.png" class="logo">
      <h1 class="warning">注意</h1>
      <h2 class="warning">
        <p>・本作品は、2021年度灘校文化祭において本校で実地開催される企画と同じ内容となっております。実地企画に参加される予定のある方は、本作品をご覧にならないように推奨しております。</p>
        <p>・問題を解く上で問題を印刷すると便利なこともございますので、スクリーンショットを撮るなどして各自で印刷をしてください。</p>
        <p>・SNSやブログなど、インターネット上でのネタバレはご遠慮ください。</p>
      </h2>
      <h1 class="story">Story</h1>
      <p class="story">あなたは有名芸能人。母校の文化祭のステージ企画に参加するため楽屋にいたところ、何者かに外から鍵をかけられてしまった。部屋に残されていたのはマネージャーからのメモ数枚と鍵のついた箱のみ。謎を解いて、楽屋から脱出せよ</p>
      <riddle v-for="riddle in riddles.slice(0,progress+1)" :key="riddle.id" v-bind="riddle" @clear="clear($event)" @open_hint="open_hint($event)" @print="print_img($event)" class="riddle"></riddle>
    </main>
    <transition name="fade" class="last">
      <div class="last-content">
        <h1 v-if="completed" class="last">ドッキリ</h1>
        <h1 v-if="completed" class="last">大成功！</h1>
        <p v-if="completed" class="last">プレイいただきありがとうございました。</p>
      </div>
    </transition>
  </div>
</template>

<script>
import Riddle from './components/Riddle.vue'
import HintModal from './components/HintModal.vue'

export default {
  name: 'App',
  data: function(){
    return {
      riddles:[
        {
          id: 0,
          clues: [
            {id: 0,img_url: '/calendar.jpg'},
            {id: 1,img_url: '/memo.jpg'},
            {id: 2,img_url: '/first_riddles/1-Fri.jpg',hint: "イラストを漢字に直して比較してみましょう"},
            {id: 3,img_url: '/first_riddles/1-Sat.jpg',hint: "6/26,27に関連するものをたどってみましょう"},
            {id: 4,img_url: '/first_riddles/1-Wed.jpg',hint: "矢印方向に熟語ができるようにしましょう"},
            {id: 5,img_url: '/first_riddles/1-Mon.jpg',hint: "単語の始まりと終わりに注目しましょう"},
            {id: 6,img_url: '/first_riddles/1-Sun.jpg',hint: "二字熟語が6つできるようにしましょう"},
            {id: 7,img_url: '/first_riddles/1-Thu.jpg',hint: "図形の欠けた部分に注目しましょう"},
            {id: 8,img_url: '/first_riddles/1-Tue.jpg',hint: "漢字の一部に注目しましょう"}
          ],
          hint: "初めの週を見て、それをカレンダーから探し出しましょう",
          answers: ["642","６４２","ろくよんに","ロクヨンニ"]
        },
        {
          id: 1,
          clues: [
            {id: 0,img_url: '/menu.jpg'},
            {id: 1,img_url: '/skeleton.jpg'}
          ],
          hint: "文字数の多い語から埋めてみましょう",
          answers: ["消しゴム","消ゴム","けしごむ","ケシゴム","けしゴム"]
        },
        {
          id: 2,
          clues: [
            {id: 0,img_url: '/last.jpg'}
          ],
          hint: "漢字の一部に注意してみましょう\n今まで参照したものにも気を配りましょう",
          answers: ["ドッキリ大成功","どっきり大成功","ドッキリだいせいこう","どっきりだいせいこう","ドッキリダイセイコウ"]
        }
      ],
      last_riddle_id: 2,
      progress: 0,
      isHintShowed: false,
      hint_content: "no_hint",
      completed: false
    }
  },
  components: {Riddle,HintModal},
  methods: {
    clear: function(id){
      if(id == this.last_riddle_id){
        this.complete()
      }else{
        this.progress += 1
      }
    },
    complete: function(){
      this.completed = true
    },
    open_hint: function(content){
      this.hint_content = content
      this.isHintShowed = true
    },
    close_hint: function(){
      this.isHintShowed = false
    }
  }
}
</script>

