<template>
  <div id="riddle" :class="{ cleared: isCleared }">
    <h1 class="title">第{{ id+1 }}の謎</h1>
    <carousel
    :per-page="1"
    paginationPosition="bottom-overlay"
    >
      <slide v-for="clue in clues" :key="clue.id">
        <clue v-bind="clue" class="clue" @open_hint="open_hint($event)"></clue>
      </slide>
    </carousel>
    <input type="text" v-model="user_ans" :disabled="isCleared" @keypress.enter="send_answer" class="ans_input" placeholder="答えを入力">
    <button @click="send_answer" :disabled="isCleared" class="ans_button">送信</button>
    <button v-if="hint != 'no_hint'" @click="open_hint(hint)" class="hint-button">ヒント</button>
    <transition name="fade">
      <h1 v-if="isCleared" class="clear_message">正解！</h1>
    </transition>
    <transition name="fade">
      <h2 v-if="incorrect" class="x">×</h2>
    </transition>
  </div>
</template>

<script>
import Clue from './Clue.vue'
import { Carousel, Slide } from 'vue-carousel'

export default {
  name: 'Riddle',
  data: function(){
    return {
      user_ans: "",
      isCleared: false,
      incorrect: false
    }
  },
  props: {
    id: Number,
    clues: Array,
    answers: Array,
    hint: {
      type: String,
      default: 'no_hint'
    }
  },
  methods: {
    send_answer: function(){
      if(this.answers.includes(this.user_ans)){
        this.clear()
      }else{
        this.incorrect = true
        setTimeout(() => {
          this.incorrect = false}
          ,3000
        )
      }
    },
    clear: function(){
      this.isCleared = true
      this.$emit('clear',this.id)
    },
    open_hint: function(content){
      this.$emit('open_hint',content)
    }
  },
  components: {Clue,Carousel,Slide}
}
</script>

